@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Lobster&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Arvo:ital@1&display=swap);
 *{
     padding: 0;
     margin: 0;
 }
 .App{
     width: 100vw;
     height: 100vh;
     padding: 100px; 
     background-image: url(/weather-app-react/static/media/bg.80e1fdc8.png);
     background-size: 100% 100%;
     background-repeat: no-repeat;
 }
 @media(max-width:600px){
     .App{
         padding: 10px;
     }
 }
.footer{
    background-color:rgba(0,0,0,0.7); 
    width: 100vw;  
    position: fixed;
    bottom: 0; 
}
.footer p{
    margin:20px;
    color: white;
    text-align: center;
    font-size: 18px;
}
.footer p a{
    color: white;
}
.footer p a:hover{
    color: yellow;
    text-decoration: none;
}
@media(max-width : 600px)
{
    .footer p{
     font-size: 15px;
    }
}
.inp{ 
    width: 100%;
    margin: auto;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}
input[type="text"]{ 
    flex: 3 1; 
    margin: 10px;
    border-radius: 10px;
    height: 45px;
}
button{
    flex: 1 1;  
    height: 43px; 
}
@media(max-width:600px){
    .inp{
        width:80vw;
    }
}
.result{
    width: 100%;
    padding: 10px;
}
.name{
    display: flex;
    flex-direction: row; 
    justify-content: center;
    align-items: center;
}
.name h1{ 
    font-family: 'Bebas Neue', cursive;
}
.name h2{ 
    font-family: 'Bebas Neue', cursive;
    color: rgb(82, 78, 78);
}
.temp h1{
    text-align: center;
    font-weight: bold;    
    font-family: 'Lobster', cursive;
    font-size: 50px;
}  
.details{
    width: 100%;
    height: auto;
    padding: 10px;  
    margin: 4px;
}
.detail-div{
    box-shadow: 2px 2px 8px black; 
    margin-bottom: 10px;
    height: 200px; 
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.detail-div p{
    font-size: 20px; 
    font-weight: bolder;
    font-family: 'Arvo', serif; 
    color: rgb(5, 5, 5);
}
@media(max-width:600px)
{
    .detail-div{
        height: 100px;
        border-radius: 20px;
    }
    .temp h1{
        font-size:30px;
    }
}
